import React from "react";
import html2canvas from "html2canvas";
import { Link } from "react-router-dom";
import { jsPDF } from "jspdf";

const makePNG = () => {
  var printCanvas = document.getElementsByClassName("Canvas")[0];
  /*
  var designCanvas = document.getElementsByClassName("Canvas")[0];
  var printCanvas = designCanvas.cloneNode(true);
  printCanvas.style.transform = "scale(2.5)";
  printCanvas.style.position = "absolute";
  document.body.prepend(printCanvas)
  /* */

  var time = new Date().valueOf();
  var fileName = `M&M-custom-${time}`;

  function renderError(error) {
    console.log(error);
  }
  /*
  const oldIMG = document.getElementById("pic");
  if(oldIMG){
    console.log("remove")
    oldIMG.remove()
  } 
  */

  function renderComplete(canvas) {
    let imgData = canvas.toDataURL("image/png");
    var doc = new jsPDF('p', 'in', [6, 6]);
    doc.addImage(imgData, "PNG", 0, 0, 6, 6);
    doc.save(`${fileName}.pdf`);

    //var download = document.createElement("a");
    //download.href = imgData;
    //download.download = pngName;
    //download.click();
    
    //var image = new Image();
    //image.id  = "pic";
    //image.src = canvas.toDataURL("image/png");

    //document.querySelector('.Canvas').appendChild(image);
    //document.querySelector("#pic").style.opacity = 0.5;
    //printCanvas.remove()
  }

  //https://html2canvas.hertzen.com/features/
  html2canvas(printCanvas, {
    useCORS: true,
    scale: 2.25,
    timeout: 15000,
    allowTaint: true,
    backgroundColor: null,
    height: printCanvas.offsetHeight,
    width: printCanvas.offsetWidth,
  }).then(renderComplete)
    .catch(renderError);

  console.log("click");
};

export const BtnMakePdf = () => (
  <div className="BTN" onClick={makePNG}>
    <b>Save PDF</b>
  </div>
);

export const BtnBack = () => (
  <Link to="/" className="BTN">
    <b>Back</b>
  </Link>
);
